import React from 'react'
import { ZONES } from '../../types'

interface SerializerCustomProps {
  bold?: boolean
  color?: string
  fontSize?: string
  withMargins?: boolean
  inline?: boolean
  content?: {}
}

export const ConfigurableCmsHtmlSerializer = (customProps: SerializerCustomProps = {}, slice: any, children: any)=> {
  const serialize = {
    [ZONES.CALL_TO_ACTION_PANEL]: (slice: any, children: any) => null,
    [ZONES.HEADER]: (slice: any, children: any) => null,
    [ZONES.FOOTER]: (slice: any, children: any) => null,
    [ZONES.GENERIC_SECTION]: (slice: any, children: any) => null,
    [ZONES.STATIC_PLAYLIST]: (slice: any, children: any) => null,
    [ZONES.STATIC_PAGE_CONNECTION]: (slice: any, children: any) => null,
    [ZONES.MULTI_COLUMN_SECTION]: (slice: any, children: any) => null,
  }

  return serialize
}
